

/* Tab Container */
.Tabs {
width: 55%;
height: auto;
min-height: 400px;
background: #053742;
margin: 1.7rem 1rem 1.5rem 0rem;
padding: 2rem 1rem;
color: #E8F0F2;
border-radius: 2rem;
@media (max-width: 769px) {
    padding: 2rem 0;
}

}

/* Tab Navigation */
ul.nav {
width: 60%;
margin: 0 auto 2rem;
display: flex;
align-items: center;
justify-content: space-between;
border: 1px solid #39A2DB;
border-radius: 2rem;
padding-left: 0px;
@media (max-width: 768px) {
    width: 90%;
}
}

ul.nav li {
width: 50%;
padding: 1rem;
list-style: none;
text-align: center;
cursor: pointer;
transition: all 0.7s;
}

ul.nav li:first-child {
border-bottom-left-radius: 2rem;
border-top-left-radius: 2rem;
}

ul.nav li:last-child {
border-bottom-right-radius: 2rem;
border-top-right-radius: 2rem;
}

ul.nav li:hover {
background: rgba(50, 224, 196, 0.15);
}

ul.nav li.active {
background: #39A2DB;
}

/* Tab Content Styles */
.TabContent {
font-size: 2rem;
text-align: center;
}